body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  background: url("media/bgr.jpg");
  background-position:top;
  background-size:cover;
  margin: 0px;
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  background-color: black;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

h3, h4 {
  margin-bottom: 0;
}

nav {    /* border-bottom: 4px solid black; */
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.75);
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {    background: #3d83ac;
  color: white;
}

#page-body {    max-width: 700px;
  margin: auto;
  background: white;
  padding: 70px;
  /* padding-top: 64px; */
  margin-top: 122px;
}
#page-body h1{ color: #cecece; }
#page-body h3{ color: #444; }

.article-list-item {
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  padding-bottom: 32px;
  border-bottom: 2px solid #666;
}

#add-comment-form {
  border-bottom: 2px solid black;
  padding: 16px;
}

#add-comment-form h3 {
  margin-bottom: 16px;
}

#add-comment-form input, textarea {
  display: block;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  border: 2px solid black;
  margin-bottom: 16px;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}

.comment {
  border-bottom: 2px solid black;
  padding: 16px;
}

.icbutton{    color: #444;
  text-decoration: none;
  font-weight: 600;}
.icbutton:hover{text-decoration: none;}